<template>
  <div class="component-wrapper-class">
    <span v-if="showScroolText" style="white-space: nowrap; color:red; text-align: left; position: relative; left: 0;" ref="st">{{msg}}</span>
    <scroll-wrapper class="bs-wrapper-default-class">

      <div class="bscroll-content-class">
        <div v-for="(outline,index) in outlineList "
             @click="onClickOutline(index)"
             class="x-cell">
          <div class="x-location-line">
            <img src="../assets/location.png" style="width: 25px;"/>
            <div style="
          flex: 1;
          justify-content: space-between;
          display: flex;
          flex-direction: row;">
              <span style="">{{outline.LOCATION}}</span>
              <!--<span style="
              white-space: nowrap;
              display: flex;
              flex-direction: column;
              justify-content: center;
              align-items: center;">10-19</span>-->
            </div>
          </div>
          <h1 class="x-outline-title">{{outline.TITLE}}</h1>
          <span>{{outline.dateStr}}</span>
        </div>

        <div class="page-menu">
          <!--{{upPageDisable == true?'page-menu-item page-menu-item-disable':'page-menu-item'}}-->
          <div :class="upPageDisable == true?'page-menu-item page-menu-item-disable':'page-menu-item'" @click="onClickUpPage">上一页</div>
         <!-- <div class="page-menu-item">当前1/94页</div>-->
          <!--{{nextPageDisable == true?'page-menu-item page-menu-item-disable':'page-menu-item'}}-->
          <div :class="nextPageDisable == true?'page-menu-item page-menu-item-disable':'page-menu-item'" @click="onClickNextPage">下一页</div>
        </div>



      </div>


    </scroll-wrapper>

   <!-- <div class="refresh-item">
      <img src="../assets/refresh.png" style="height: 50%;width: 50%"/>
    </div>-->
  </div>
</template>



<script>
  import TSCenter from '../utils/TempStorageCenter'
  import ScrollWrapper from '../components/BScrollWrapper'
  import { Toast, Indicator, MessageBox} from 'mint-ui'
  export default {
    name: 'outlineList',
    components: {
      'scroll-wrapper': ScrollWrapper
    },
    data(){
      return {
        outlineList: [],
        pageNumber: 1,
        pageSize: 20,
        lastPageNumger: 1000000,
        msg: "本平台免费更新住房公寓酒店转让信息，所有信息由网友提供，请自行甄别，避免上当受骗。由于市场变化，本平台不保证所有信息的及时性和准确性。",
        interval: null,
        showScroolText: true
      }
    },
    computed: {
      upPageDisable() {
        let _this = this
        if (_this.pageNumber == 1) {
          return true
        } else {
          return false
        }
      },
      nextPageDisable() {
        let _this = this
        if (_this.pageNumber < _this.lastPageNumger) {
          return false
        } else {
          return true
        }
      }
    },
    methods: {
      // lang: function () {
      //   console.log(this.msg);
      //   var _this = this;
      //   _this.interval = setInterval(function () {
      //     //获取第一个字符
      //     var start = _this.msg.substring(0, 1);
      //     //得到后面的字符
      //     var end = _this.msg.substring(1);
      //     //重新赋值
      //     _this.msg = end + start;
      //
      //     _this.$refs.ddd.value=_this.msg
      //     // document.getElementById("clock").value = _this.msg;
      //   }, 350)
      // },
      onClickOutline(index) {
        console.log('clickOutline index is=>', index)
        let _this = this

        let objectId = _this.outlineList[index].objectId
        console.log('set objectId =>', objectId)

        // location.href = `/HouseDetail?objectId=${objectId}`

        TSCenter.sharedInstance().setGoodsWithKey('objectId', objectId)

        _this.$router.push({
          path: `/HouseDetail?objectId=${objectId}endobjectId`
          // path: `/HouseDetail`
        })

        // window.open('https://www.jianshu.com/p/ec380533f659')
      },
      onClickUpPage() {
        let _this = this;
        if (_this.upPageDisable == true) {
          return
        }
        _this.pageNumber = _this.pageNumber - 1;
        _this.reqDataFromServe(_this.pageSize,_this.pageNumber);
      },
      onClickNextPage() {
        let _this = this;
        if (_this.nextPageDisable == true) {
          return
        }
        _this.pageNumber = _this.pageNumber + 1;
        _this.reqDataFromServe(_this.pageSize,_this.pageNumber);
      },
      reqDataFromServe(pageSize, pageNumber) {
        Indicator.open({
          text: '',
          spinnerType: 'triple-bounce'
        });

        let _this = this
        const query = _this.Bmob.Query('CZW_ARTICLE');
        query.order("-createdAt");
        query.select("TITLE","LOCATION");
        query.limit(pageSize);
        query.skip(pageSize*(pageNumber-1));
        query.find().then(res => {
          Indicator.close();
          // 返回成功
          console.log("查询出租屋文章标题成功=>", res)
          _this.outlineList = res
          for(let i = 0; i < _this.outlineList.length; i ++){
            let item = _this.outlineList[i]
            let date = _this.__parserDate(item.createdAt)

            item.dateStr = _this.__formatDateStr(date)
          }

          if (_this.outlineList.length < _this.pageSize) {
            _this.lastPageNumger = _this.pageNumber
          }
        }).catch(function (err) {
          Indicator.close();
          console.log("查询出租屋文章标题失败!!!=>", err)
        })
      },

      __parserDate (dateStr) {
        var t = Date.parse(dateStr);
        if (!isNaN(t)) {
          return new Date(Date.parse(dateStr.replace(/-/g, "/")));
        } else {
          return new Date();
        }
      },

      __formatDateStr(date) {
        var y = date.getFullYear();
        var m = date.getMonth() + 1;
        m = m < 10 ? ('0' + m) : m;
        var d = date.getDate();
        d = d < 10 ? ('0' + d) : d;
        var h = date.getHours();
        var minute = date.getMinutes();
        minute = minute < 10 ? ('0' + minute) : minute;
        var second= date.getSeconds();
        second = minute < 10 ? ('0' + second) : second;
        // return y + '-' + m + '-' + d+' '+h+':'+minute+':'+ second;

        return y + '-' + m + '-' + d;
      },


      __scrollText(number){
        let _this = this;
        let count = 1;
        this.timer = setInterval(function () {
          let yd = number - 2 * count;

          if(yd < (-number)){
            _this.showScroolText = false
            clearInterval(_this.timer)
            return
            // count = 1;
            // yd = number - 2 * count;
          }

          _this.$refs.st.style.transform = 'translateX(' + yd + 'px)'
          count ++;
        }, 50)
      },
    },
    created() {
      let _this = this;
      _this.reqDataFromServe(_this.pageSize,_this.pageNumber);
    },
    mounted() {
      let st_width = this.$refs.st.getBoundingClientRect().width;
      console.log('宽度为：',st_width)
      let st_right =this.$refs.st.getBoundingClientRect().right;
      console.log('离右边的距离：',st_right)
      let st_left =this.$refs.st.getBoundingClientRect().left;
      console.log('离左边的距离：',st_left)
      let h = st_width + st_left - 1
      console.log('总和：',h)

      this.$refs.st.style.transform = 'translateX(' + h + 'px)'

      this.__scrollText(h)
    }
  }
</script>

